<template>
  <div class="progressControl">
    <div class="content">
      <div class="box" ref="changeBox"></div>
      <div class="progress-box">
        <input type="range" ref="inputDiv" max="1" min="0" step="0.01" />
      </div>
    </div>
  </div>
</template>
<script setup>
import { onMounted, ref } from "vue";
const inputDiv = ref();
const changeBox = ref();
onMounted(() => {
  console.log(inputDiv.value);
  let doc = inputDiv.value;
  doc.oninput = changeInput;
  changeInput();
});
const changeInput = () => {
  console.log(inputDiv.value.value);
  changeBox.value.style.setProperty("--delay", `${-inputDiv.value.value}s`);
};
</script>
<style lang="scss" scoped>
.progressControl {
  .content {
    padding: 200px;
    @keyframes play {
      to {
        // transform: translateX(200px) scale(1.5);
        // background: #008c8c;
        clip-path: polygon(0 0, 75% 100%, 100% 100%, 100% 0);
      }
    }
    .box {
      --delay: 0s;
      width: 100px;
      height: 100px;
      margin-bottom: 20px;
      background-color: aqua;
      border-radius: 50%;
      animation: play 1s forwards paused;
      animation-delay: var(--delay);
    }

    .progress-box {
      text-align: left;
      input {
      }
    }
  }
}
</style>